@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
@import url(https://fonts.googleapis.com/css?family=Monoton&display=swap);
body {
  margin: 0;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
  border: none;
}

.App {
  font-family: 'Source Sans Pro', Arial, sans-serif;
}

a {
  text-decoration: none;
  color: #fff;
  cursor: pointer;
}

/* utillity classes */

.flex {
  display: flex;
}

.flexCenter {
  justify-content: center;
  align-items: center;
}

.grid {
  display: grid;
}

.block {
  display: block;
}

.p1 {
  padding: 1rem;
}

.container {
  text-align: center;
  background-color: #333;
  color: whitesmoke;
  width: 100%;
  min-height: 100vh;
}

.mb1 {
  margin-bottom: 1rem;
}

.btn {
  display: block;
  font-size: 1.1em;
  cursor: pointer;
  padding: 7px 25px;
}

.btn:hover {
  box-shadow: 2px 2px 6px rgba(150, 151, 152, 0.47);
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.radius {
  border-radius: 8px;
}

.shadow {
  box-shadow: 3px 3px 6px rgba(150, 151, 152, 0.47);
}

li {
  color: #fff;
}

.topLink {
  text-shadow: 2px 2px 4px #000;
  padding: 0.7rem 0;
  color: rgba(155, 243, 243, 0.925);
}

.topLink:hover {
  -webkit-filter: brightness(30%);
          filter: brightness(30%);
}
header {
  max-height: 10vh;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 -15px 30px -5px #c986d7;
  background: -webkit-gradient(linear, left top, right top, from(rgba(70, 68, 68, 0)), color-stop(25%, rgba(255, 255, 255, 0.048)), color-stop(75%, rgba(255, 255, 255, 0.068)), to(rgba(255, 255, 255, 0)));
  background: linear-gradient(90deg, rgba(70, 68, 68, 0) 0%, rgba(255, 255, 255, 0.048) 25%, rgba(255, 255, 255, 0.068) 75%, rgba(255, 255, 255, 0) 100%);
  -webkit-animation: shine 20s infinite;
          animation: shine 20s infinite;
}

header h4 {
  font-family: 'Monoton', cursive;
  background: radial-gradient(circle at 50% 120%, #d481f6, #cb76ef 10%, #9936d6 80%, #b12dda 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-filter: brightness(150%);
          filter: brightness(150%);
  text-shadow: #17171a 1px 1px 0;
}

nav {
  max-width: 60%;
}

.navUl li {
  padding: 0 0.5rem;
}

.navUl li:hover {
  -webkit-filter: brightness(50%);
          filter: brightness(50%);
  text-shadow: 2px 2px 4px #000;
}

@-webkit-keyframes shine {
  0% {
    box-shadow: 0 -15px 30px -5px #c986d7;
  }

  40% {
    box-shadow: 0 -5px 55px -1px #c986d7;
  }

  75% {
    box-shadow: 0 5px 55px -1px #c986d7;
  }

  100% {
    box-shadow: 0 -15px 30px -5px #c986d7;
  }
}

@keyframes shine {
  0% {
    box-shadow: 0 -15px 30px -5px #c986d7;
  }

  40% {
    box-shadow: 0 -5px 55px -1px #c986d7;
  }

  75% {
    box-shadow: 0 5px 55px -1px #c986d7;
  }

  100% {
    box-shadow: 0 -15px 30px -5px #c986d7;
  }
}

@media(min-width: 768px) {
  header h4 {
    font-size: 1.5rem;
  }
}
.loader {
  width: 50px;
  height: 50px;
  margin: 2rem 0;
  border: 2px solid #41f24a;
  border-top: 2px ridge #1b4e1d;
  border-radius: 100%;
  -webkit-animation: spin 1s infinite linear;
          animation: spin 1s infinite linear;
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media(min-width: 768px) {
  .loader {
    width: 80px;
    height: 80px;
    margin: 3rem 0;
  }
}
.biggerImage {
  height: 200px;
  margin: 0.3rem auto;
  padding: 0.8rem;
  max-width: 100%;
}

.singleMovie {
  padding: 0.8rem;
}

.otherDetails {
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  text-align: left;
  padding: 0.8rem;
}

.film {
  padding: 0.8rem 0;
  box-shadow: 3px 3px 6px rgba(150, 151, 152, 0.47);
}

.info-poster {
  background: radial-gradient(circle at top left, rgba(66, 65, 63, 0.833), rgba(26, 23, 20, 0.675));
  box-shadow: 3px 3px 6px rgba(150, 151, 152, 0.47);
}

.addMovieBtn {
  margin: 0.5rem auto;
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(43, 181, 142)), color-stop(52%, rgb(7, 84, 57)), color-stop(54%, rgb(8, 86, 61)), color-stop(70%, rgb(13, 105, 66)), to(rgb(43, 181, 132)));
  background: linear-gradient(to bottom, rgb(43, 181, 142) 0%, rgb(7, 84, 57) 52%, rgb(8, 86, 61) 54%, rgb(13, 105, 66) 70%, rgb(43, 181, 132) 100%);
  color: #fff;
}

.handIcon {
  display: inline-block;
  font-size: 1rem;
}

@media(min-width: 768px) {
  .info-poster {
    display: flex;
    width: 100%;
    justify-content: space-around;
  }

  .addMovieBtn {
    margin-top: 1.2rem;
  }

  .singleMovie {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .biggerImage:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition: 0.5s ease-in-out;
    transition: 0.5s ease-in-out;
    -webkit-filter: sepia(50%);
            filter: sepia(50%);
  }
}

@media(min-width: 992px) {
  .biggerImage {
    height: 250px;
  }
}
.SingleMovieBox {
  width: 100%;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.movieBox {
  background: radial-gradient(circle at top left, rgba(66, 65, 63, 0.833), rgba(26, 23, 20, 0.675));
  ;
  border: 2px inset white;
  box-shadow: 2px 2px 2px white;
}

.btnDetailsWrapper {
  height: 50px;
  padding: 1rem 0;
  cursor: pointer;
  grid-area: btnDetails;
  background: -webkit-gradient(linear, left top, right top, from(#ae96c73f), color-stop(25%, #7bc7c352), color-stop(50%, #80addc4f), color-stop(75%, #a27ec744), to(#b277a54b));
  background: linear-gradient(90deg, #ae96c73f 0%, #7bc7c352 25%, #80addc4f 50%, #a27ec744 75%, #b277a54b 100%);
  text-shadow: 2px 2px 4px #000;
  mask-image: linear-gradient(100deg, rgba(255, 255, 255, 0) 0%, #fff 25%, #fff 75%, rgba(255, 255, 255, 0) 100%);
  -webkit-mask-image: linear-gradient(100deg, rgba(255, 255, 255, 0) 0%, #fff 25%, #fff 75%, rgba(255, 255, 255, 0) 100%);
}

@supports (-ms-ime-align:auto) {
  .btnDetailsWrapper {
    background: rgba(201, 135, 218, 0.445);
    -webkit-filter: brightness(80%);
            filter: brightness(80%);
  }
}

.arrow {
  color: rgba(214, 212, 206, 0.966);
  font-size: 1.2rem;
  margin-left: 0.5rem;
  -webkit-animation: rotate 5s infinite;
          animation: rotate 5s infinite;
}

.movieBox {
  grid-template-columns: 3fr 5fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 30px;
  grid-template-areas:
    'smallImage movieInfo'
    'smallImage movieInfo'
    'smallImage movieInfo'
    'smallImage movieInfo'
    'btnDetails btnDetails'
  ;
  min-height: 200px;
}

.smallImage {
  height: 100px;
  max-width: 100%;
  grid-area: smallImage;
}

.movieInfo {
  grid-area: movieInfo;
  flex-direction: column;
  align-items: flex-start;
  border-bottom: 2px inset #fff;
  text-align: left;
}

.movieTitle {
  font-size: 0.9rem;
  font-weight: 700;
}

.movieYear,
.movieType {
  font-size: 0.7rem;
  font-weight: 500;
}

@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
  }

  to {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
  }
}

@keyframes rotate {
  from {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
  }

  to {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
  }
}

@media(min-width: 400px) {
  .movieTitle {
    font-size: 1.3rem;
  }

  .movieYear,
  .movieType {
    font-size: 1rem;
  }
}

@media(min-width: 600px) {
  .movieBox {
    grid-template-columns: 1fr 4fr;
    grid-template-rows: 1fr 1fr 30px;
    grid-template-areas:
      'smallImage movieInfo'
      'smallImage movieInfo'
      'btnDetails btnDetails'
    ;
    max-height: 200px;
  }

  .smallImage {
    height: 120px;
    grid-area: smallImage;
    margin: 0 auto;
  }

  .movieInfo {
    justify-content: flex-start;
  }

  .btnDetailsWrapper:hover {
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
  }
}
.renderResults {
  max-width: 1000px;
}

.detailsContainer {
  border: 2px inset #fff;
}
.search h1 {
  font-size: 1rem;
}

.search h3 {
  font-size: 0.7rem;
}

.searchForm {
  width: 100%;
  height: 100%;
}

.searchArea {
  -webkit-transition: width 0.5s ease-in-out;
  transition: width 0.5s ease-in-out;
  width: 200px;
  padding: 0.6rem;
  background: lightgray;
  font-size: 1.1rem;
  color: #333;
}

.btnSearch {
  margin: 0 auto;
  color: rgb(82, 33, 83);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #e2cff3fb), to(#c6a3e2f1));
  background: linear-gradient(to bottom, #e2cff3fb 5%, #c6a3e2f1 100%);
  outline: 2px solid rgb(82, 33, 83);
}

.btnSearch:hover {
  color: #fff;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #dfbdfa), to(#bc80ea));
  background: linear-gradient(to bottom, #dfbdfa 5%, #bc80ea 100%);
  outline: none;
}

@media(min-width: 500px) {
  .searchArea:focus {
    width: 400px;
  }
}

@media(min-width: 768px) {
  .search h1 {
    font-size: 1.5rem;
  }

  .search h3 {
    font-size: 1rem;
  }
}
.watchlist {
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.listContainer {
  width: 95vw;
  background: radial-gradient(circle at top left, rgba(66, 65, 63, 0.833), rgba(26, 23, 20, 0.675));
  box-shadow: 3px 3px 6px rgba(150, 151, 152, 0.47);
}

h1 {
  padding: 1rem;
}

.buttons,
.list,
.imdbWrapper,
.titlesWrapper,
.favWrapper {
  flex-direction: column;
  padding: 0.8rem;
}

.imdbWrapper,
.titlesWrapper,
.favWrapper {
  height: 100%;
  justify-content: flex-start;
}

.btnSort {
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(120, 119, 128)), color-stop(0%, rgb(59, 58, 68)), color-stop(0%, rgb(130, 135, 161)), color-stop(49%, rgb(65, 62, 66)), color-stop(50%, rgb(62, 59, 63)), to(rgb(140, 140, 156)));
  background: linear-gradient(to bottom, rgb(120, 119, 128) 0%, rgb(59, 58, 68) 0%, rgb(130, 135, 161) 0%, rgb(65, 62, 66) 49%, rgb(62, 59, 63) 50%, rgb(140, 140, 156) 100%);
  color: white;
  margin: 1em auto;
  width: 155px;
  font-size: 0.8rem;
  padding: 10px 0;
  display: block;
  margin: 1em auto;
  cursor: pointer;
}

.listItem {
  width: 95%;
  height: 100%;
  border: 1px solid white;
  border-radius: 10px 40px 10px 40px;
  margin: 0.5rem;
  align-items: center;
  justify-items: center;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1.5fr, 1fr, 1fr, 1fr;
  grid-template-areas:
    '. listImage .'
    'titlesWrapper titlesWrapper titlesWrapper'
    'imdbWrapper imdbWrapper imdbWrapper'
    'favWrapper favWrapper favWrapper';
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(76, 76, 76, 1)), color-stop(0%, rgba(102, 102, 102, 1)), color-stop(0%, rgba(102, 102, 102, 1)), color-stop(12%, rgba(89, 89, 89, 0.97)), color-stop(54%, rgba(17, 17, 17, 0.86)), color-stop(76%, rgba(43, 43, 43, 0.8)), color-stop(82%, rgba(44, 44, 44, 0.79)), color-stop(91%, rgba(28, 28, 28, 0.76)), to(rgba(19, 19, 19, 0.74)));
  background: linear-gradient(to top, rgba(76, 76, 76, 1) 0%, rgba(102, 102, 102, 1) 0%, rgba(102, 102, 102, 1) 0%, rgba(89, 89, 89, 0.97) 12%, rgba(17, 17, 17, 0.86) 54%, rgba(43, 43, 43, 0.8) 76%, rgba(44, 44, 44, 0.79) 82%, rgba(28, 28, 28, 0.76) 91%, rgba(19, 19, 19, 0.74) 100%);
  background: -moz-linear-gradient(to top, rgba(76, 76, 76, 1) 0%, rgba(102, 102, 102, 1) 0%, rgba(102, 102, 102, 1) 0%, rgba(89, 89, 89, 0.97) 12%, rgba(17, 17, 17, 0.86) 54%, rgba(43, 43, 43, 0.8) 76%, rgba(44, 44, 44, 0.79) 82%, rgba(28, 28, 28, 0.76) 91%, rgba(19, 19, 19, 0.74) 100%);
}

.titlesWrapper {
  grid-area: titlesWrapper;
}

.listImage {
  height: 100px;
  grid-area: listImage;
  margin: 0.7rem auto;
}

.imdbWrapper {
  grid-area: imdbWrapper;

}

.favWrapper {
  grid-area: favWrapper;
}

.addFav,
.imdbLink,
.delete,
.hash {
  cursor: pointer;
}

.imdbLink {
  color: rgba(200, 255, 0, 0.89);
  text-decoration: underline;
}

.btnList {
  color: whitesmoke;
  padding: 0.3rem;
  font-size: 0.8rem;
  width: 120px;
}

.addFav {
  border: 1px solid rgb(21, 201, 15);
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(43, 181, 142)), color-stop(52%, rgb(7, 84, 57)), color-stop(54%, rgb(8, 86, 61)), color-stop(70%, rgb(13, 105, 66)), to(rgb(43, 181, 132)));
  background: linear-gradient(to bottom, rgb(43, 181, 142) 0%, rgb(7, 84, 57) 52%, rgb(8, 86, 61) 54%, rgb(13, 105, 66) 70%, rgb(43, 181, 132) 100%);
}

.addFav:disabled {
  opacity: 0.2;
}

.star {
  color: rgb(72, 73, 66);
  font-size: 1.7rem;
  padding: 0.4rem;
}

.yellow {
  color: rgb(239, 243, 8);
}

.delete {
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(181, 43, 43)), color-stop(52%, rgb(84, 12, 7)), color-stop(54%, rgb(86, 17, 8)), color-stop(70%, rgb(105, 19, 13)), to(rgb(181, 43, 43)));
  background: linear-gradient(to bottom, rgb(181, 43, 43) 0%, rgb(84, 12, 7) 52%, rgb(86, 17, 8) 54%, rgb(105, 19, 13) 70%, rgb(181, 43, 43) 100%);
  margin-top: 1.7rem;
}

@media(min-width: 600px) {
  .buttons {
    flex-direction: row;
  }

  .listItem {
    grid-template-columns: 1fr 2fr 1fr 1fr;
    grid-template-rows: 1fr;
    grid-template-areas:
      'listImage titlesWrapper imdbWrapper favWrapper';
  }

  .imdbWrapper {
    flex-direction: column;
    justify-content: flex-start;
  }

  .hash {
    padding: 0.5rem;
    border: 1px solid rgba(155, 243, 243, 0.925);
  }
}

@media(min-width: 768px) {

  .btnSort:hover,
  .addFav:hover,
  .delete:hover {
    box-shadow: 2px 2px 6px rgba(150, 151, 152, 0.47);
    -webkit-transition: 0.5s;
    transition: 0.5s;
  }

  .imdbLink:hover {
    -webkit-filter: brightness(40%);
            filter: brightness(40%);
  }
}

@media(min-width: 992px) {
  .listImage {
    height: 120px;
  }
}

@media(min-width: 1200px) {
  .watchlist {
    font-size: 110%;
  }

  .listContainer {
    max-width: 1100px;
  }
}
